import Vue from 'vue'
import Vuex from 'vuex'
import App from './App.vue'
import Router from 'vue-router'
import routes from './route'
import store from './store'

import './assets/css/reset.css'
import './assets/css/common.css'

const router = new Router({
	mode: 'history',
	scrollBehavior: () => ({y: 0}),
	routes
})

Vue.use(Router)

new Vue({
	el: '#app',
	store,
	router,
	render: h=>h(App)
})



