<template lang="html">
    <header class="header" :style="{ backgroundColor: bgColor }">
        <div class="wrapper">
            <h1 class="site-logo clearfix">
                <router-link :to="{ name: 'home', params: {} }"><img src="http://placehold.it/147x47?text=logo" alt="wecare"></router-link>
            </h1>
            <router-link :to="{ name: '', params: {} }" class="link-diagnosis">    </router-link>

            <nav class="gnb">
                <ul>
                    <li v-show="isLogged">
                        <router-link :to="{ name: 'login', params: {} }"></router-link>
                    </li>
                    <li>
                        <router-link :to="{ name: '', params: {} }"></router-link>
                    </li>
                    <li>
                        <router-link :to="{ name: '', params: {} }">
                            <i class="icon icon-mypage"></i>
                            
                        </router-link>
                    </li>
                </ul>
            </nav>
        </div>
    </header>
</template>

<script>
export default {
    props: ['bgColor'],
    computed: {
        isLogged() {
            return this.$store.state.isLogged
        }
    }
}
</script>

<style lang="scss">
.header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 108px;
    line-height: 108px;
    color: #fff;
    .site-logo {
        float: left;
        img {
            vertical-align: middle;
        }
    }
    .link-diagnosis {
        display: inline-block;
        margin-left: 20px;
        padding: 15px 20px;
        height: 50px;
        line-height: 1;
        border: 2px solid #fff;
        border-radius: 100px;
        box-sizing: border-box;
        vertical-align: middle;
    }
    .gnb {
        float: right;
        li {
            float: left;
            margin-left: 50px;
            &:first-child {
                margin-left: 0;
            }
            .icon {
                margin-right: 6px;
            }
        }
    }
}
</style>



