<template lang="html">
    <section class="main">
        <section style="background-color: #f7f7f7;">
            <div class="wrapper">
                <h3 class="main-title">  5 !</h3>
                <UseWay />
            </div>
        </section>
        <section>
            <div class="wrapper">
                <h3 class="main-title">  </h3>
                <OurExperts />
            </div>
        </section>
        <section style="background-color: #0ac5aa;">
            <div class="wrapper">
                <h3 class="main-title">  </h3>
                <UseReview />
            </div>
        </section>
    </section>
</template>

<script>
import UseWay from './Main/UseWay'
import OurExperts from './Main/OurExperts'
import UseReview from './Main/UseReview'

export default {
    components: {
        UseWay,
        OurExperts,
        UseReview
    }
}
</script>

<style lang="scss">
.main {
    > section {
        padding-top: 120px;
        padding-bottom: 94px;
    }
    .main-title {
        margin-bottom: 60px;
        padding-bottom: 30px;
        position: relative;
        font-size: 30px;
        color: #4a4a4a;
        text-align: center;
        &:after {
            content: '';
            display: block;
            position: absolute;
            top: 100%;
            left: 50%;
            margin-left: -20px;
            width: 40px;
            height: 5px;
            border-radius: 18px;
            background-color: #f7d14c;
        }
    }
}
</style>



